.item {
    margin: 1rem 0;
    list-style: none;
  }
  
  .image {
    width: 100%;
    height: auto;
    overflow: hidden;
    border-top-right-radius: 6px;
    border-top-left-radius: 6px;
  }
  
  .image img {
    width: 100%;
    object-fit:contain;
  }
  
  .content {
    justify-items: center;
    text-align: center;
    padding: 1rem;
  }
  
  .title{
    font-size: 1.25rem;
    color: #2c292b;
  }
  
  .actions {
    padding: 1.5rem;
    text-align: center;
  }
  
  .actions button {
    font: inherit;
    cursor: pointer;
    color: #77002e;
    border: 1px solid #77002e;
    background-color: transparent;
    padding: 0.5rem 1.5rem;
    border-radius: 4px;
  }
  
  .actions button:hover,
  .actions button:active {
    background-color: #ffe2ed;
  }

/* Responsive mobile styles */
@media (max-width: 480px) {
  .image {
    height: auto;
  }

  .title {
    font-size: 1rem;
  }

  .actions {
    padding: 1rem;
  }

  .actions button {
    padding: 0.5rem 1rem;
  }
}

/* Responsive tablet styles */
@media (max-width: 768px) {
  .image {
    height: auto;
  }
}

/* Responsive larger screen styles */
@media (min-width: 1200px) {
  .image {
    height: auto;
  }

  .title {
    font-size: 1.5rem;
  }
}
